import React from 'react';
import {
  AppBar,
  Toolbar,
  TextField,
  Tabs,
  Tab,
  FormControlLabel,
  Checkbox,
  Button,
  IconButton,
  Box,
  InputAdornment,
  Grid,
  Tooltip,
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import SettingsIcon from '@mui/icons-material/Settings';
import HistoryIcon from '@mui/icons-material/History';
import SearchIcon from '@mui/icons-material/Search';
import { ItemStatus } from './types'; // Assuming types.ts is correctly defined

// Define the props interface for ControlSurface
interface ControlSurfaceProps {
  // Props from TopBar
  onAddItem: () => void;
  onOpenSettings: () => void;
  onOpenActivity: () => void;
  // Props from FilterControls
  searchTerm: string;
  onSearchTermChange: (value: string) => void;
  showFlaggedOnly: boolean;
  onShowFlaggedOnlyChange: (checked: boolean) => void;
  currentFilter: ItemStatus | 'All' | 'Late' | 'Archived'; // Ensure this type matches useStockItems usage
  onCurrentFilterChange: (newFilter: ItemStatus | 'All' | 'Late' | 'Archived') => void;
}

const ControlSurface: React.FC<ControlSurfaceProps> = ({
  onAddItem,
  onOpenSettings,
  onOpenActivity,
  searchTerm,
  onSearchTermChange,
  showFlaggedOnly,
  onShowFlaggedOnlyChange,
  currentFilter,
  onCurrentFilterChange,
}) => {
  // Handler for search input change
  const handleSearchChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    onSearchTermChange(event.target.value);
  };

  // Handler for filter tab change
  const handleFilterChange = (event: React.SyntheticEvent, newValue: ItemStatus | 'All' | 'Late' | 'Archived') => {
    onCurrentFilterChange(newValue);
  };

  // Handler for checkbox change
  const handleFlaggedChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    onShowFlaggedOnlyChange(event.target.checked);
  };

  const filterOptions: Array<ItemStatus | 'All' | 'Late' | 'Archived'> = ['All', 'Pending Delivery', 'Delivered', 'Issue', 'Late', 'Archived'];

  return (
    <AppBar position="static" color="default" elevation={0} sx={{ borderBottom: '1px solid', borderColor: 'divider' }}>
      <Toolbar disableGutters sx={{ p: 1, display: 'flex', flexWrap: 'wrap', gap: 2 }}>
        {/* Top Bar Actions */}
        <Box sx={{ display: 'flex', gap: 1, alignItems: 'center' }}>
          <Button
            variant="contained"
            startIcon={<AddIcon />}
            onClick={onAddItem}
          >
            Add Item
          </Button>
          <Tooltip title="Table Settings">
            <IconButton onClick={onOpenSettings} aria-label="Table Settings">
              <SettingsIcon />
            </IconButton>
          </Tooltip>
          <Tooltip title="User Activity Log">
            <IconButton onClick={onOpenActivity} aria-label="User Activity Log">
              <HistoryIcon />
            </IconButton>
          </Tooltip>
        </Box>

        {/* Divider or Spacer could go here if needed */}
        <Box sx={{ flexGrow: 1 }} /> {/* Spacer */}

        {/* Search and Flagged filter */}
         <Box sx={{ display: 'flex', gap: 2, alignItems: 'center', flexWrap: 'wrap' }}>
           <TextField
            variant="outlined"
            size="small"
            placeholder="Search Items"
            value={searchTerm}
            onChange={handleSearchChange}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <SearchIcon color="action" />
                </InputAdornment>
              ),
            }}
            sx={{ minWidth: '250px' }} // Ensure search has enough space
          />
           <FormControlLabel
             control={
               <Checkbox
                 checked={showFlaggedOnly}
                 onChange={handleFlaggedChange}
                 size="small"
               />
             }
             label="Show Flagged Only"
             sx={{ mr: 0 }} // Remove default margin-right
           />
         </Box>

         {/* Status Filter Tabs - occupy full width below */}
         <Box sx={{ width: '100%', borderTop: '1px solid', borderColor: 'divider', mt: 1, pt: 1 }}>
           <Tabs
             value={currentFilter}
             onChange={handleFilterChange}
             variant="scrollable"
             scrollButtons="auto"
             allowScrollButtonsMobile
             aria-label="Stock item status filter"
             // Refined styling could go here later (indicator, etc.)
           >
             {filterOptions.map((filter) => (
               <Tab key={filter} label={filter} value={filter} sx={{ textTransform: 'none', minWidth: '80px' }}/>
             ))}
           </Tabs>
         </Box>
       </Toolbar>
    </AppBar>
  );
};

export default ControlSurface; 